import {mapState} from 'vuex'

export default {
  name: "ElReward",
  props: ['url'],
  data() {
    return {
      vReward: [],
      isLoading: false,
      finished: false,
      iPageNo: 0,
      iTotal: 0,
      iSort: 1,
      aSortList: [
        {text: this.$t('Other3'), value: 1},
        {text: this.$t('Other4'), value: 2},
      ]
    }
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo,
      locale: (state) => state.locale,
    })
  },
  watch: {
    $route(v) {
      this.getList()
    },
    userInfo(v) {
      return v
    },
    locale() {
      this.aSortList = [
        {text: this.$t('Other3'), value: 1},
        {text: this.$t('Other4'), value: 2},
      ]
    }
  },
  async mounted() {
    await this.getList()
  },
  methods: {
    async load() {
      this.finished = false
      this.isLoading = true
      await this.getList();
    },
    async getList() {
      // console.log(+this.$route.query.iUserId || this.userInfo.iUserId);
      let data = {
        iPageNo: this.iPageNo,
        iPageNum: 10,
        iSort: this.iSort,
        iUserId: +this.$route.query.iUserId || this.userInfo.iUserId
      }
      let res = await this.$axios.post(`/v1/emall/${this.url}`, data)
      this.vReward = this.vReward.concat(res.vRewardInfor || res.vAnswer)
      this.iPageNo++
      this.isLoading = false
      this.iTotal = res.iTotal
      if (this.vReward.length >= this.iTotal) {
        this.finished = true
      }
      // console.log(this.vReward);

    },
    handleReset(value) {
      this.iPageNo = 0
      this.iTotal = 0
      this.finished = false
      this.vReward = []
      this.getList()
      // console.log(value);
    }
  }
}
